import React, { useState } from 'react';
import { Route, Routes } from 'react-router-dom';
import ProfileBox from './components/ProfileBox/ProfileBox'
import LoginBox from './components/LoginBox/LoginBox'
import RegisterBox from './components/RegisterBox/RegisterBox'
import './App.css';

function App() {
    const [user, setUser] = useState("Logged Out.")

    return (
        <div className="content">
                <Routes>
                    <Route exact path="/react-firebase-auth" element={<ProfileBox user={user} setUser={setUser} />}/>
                    <Route exact path="/react-firebase-auth/login" element={<LoginBox setUser={setUser} />}/>
                    <Route exact path="/react-firebase-auth/register" element={<RegisterBox setUser={setUser} />}/>
                </Routes>
        </div>
    );
}

export default App;
